const HorrizontalScroolBar = ({ data, bodyPart, setBodyPart }) => {

  return (
    <ScrollMenu>
      {data.map((item, index) => {
        return (
          <Box
            key={index}
            itemID={item.id || item}
            title={item.id || item}
            m={"0 40px"}
          >
            <BodyPart item={item} bodyPart ={bodyPart} setBodyPart = {setBodyPart}></BodyPart>
          </Box>
        );
      })}
    </ScrollMenu>
  );
};

export default HorrizontalScroolBar;
